import React, { Component } from "react";

// assets
import { ReactComponent as Logo } from "../svg/repository.svg";
import CardRepository from "./loop/cardRepository";

class RepositorySection extends Component {
  constructor() {
    super();
    this.state = {
      currentPage: 1,
      maxPage: null,
      containerHTML: "",
      card: "",
      cards: [
        {
          name: "Company profile web",
          id: "ktresna",
          tag: ["HTML", "CSS", "JS"],
          des: "my first company website that was made without using a framework and only using html, css, and javascript only",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2Fk-tresna.webp?alt=media&token=0c8ea298-202b-450c-abd1-8dd5faccdbc6",
        },

        {
          name: "Note application",
          id: "cakue",
          tag: ["HTML", "CSS", "JS"],
          des: "cakue is aplication for write someting.this app is built using reactjs, and firebase",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2FcakueProject.webp?alt=media&token=d3c03721-3ef6-4a22-88b6-978977bc0fe1",
        },

        {
          name: "Financial management app",
          id: "amadeus",
          tag: ["express", "EJS", "mongoDB"],
          des: "actually this application has not been completed because I am having problems with the login and register features",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2Famadeus.webp?alt=media&token=96157bd9-c9ed-4b78-92da-650f25db7455",
        },
        {
          name: "Company profile web",
          id: "ktresna",
          tag: ["HTML", "CSS", "JS"],
          des: "my first company website that was made without using a framework and only using html, css, and javascript only",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2Fk-tresna.webp?alt=media&token=0c8ea298-202b-450c-abd1-8dd5faccdbc6",
        },

        {
          name: "Note application",
          id: "cakue",
          tag: ["HTML", "CSS", "JS"],
          des: "cakue is aplication for write someting.this app is built using reactjs, and firebase",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2FcakueProject.webp?alt=media&token=d3c03721-3ef6-4a22-88b6-978977bc0fe1",
        },

        {
          name: "Financial management app",
          id: "amadeus",
          tag: ["express", "EJS", "mongoDB"],
          des: "actually this application has not been completed because I am having problems with the login and register features",
          img: "https://firebasestorage.googleapis.com/v0/b/portofolio-3272a.appspot.com/o/img%2Famadeus.webp?alt=media&token=96157bd9-c9ed-4b78-92da-650f25db7455",
        },
      ],
    };
    this.handleNext = this.handleNext.bind(this);
    this.handlePrev = this.handlePrev.bind(this);
  }

  // set first state data
  componentDidMount() {
    const card = document.querySelectorAll(".card-repo");
    const containerHTML = document.querySelector(".container-card");

    // handle maxPage
    // saya ingin membuat untuk ukuran hp : 1, tablet: 2, laptop: 3

    // screen sm
    let maxPage = card.length;

    // screen md
    if (window.outerWidth >= 748 && window.outerWidth < 1024) {
      const tmp = maxPage / 2;
      maxPage = Math.ceil(tmp);
      // screen lg
    } else if (window.outerWidth >= 1024) {
      const tmp = maxPage / 3;
      maxPage = Math.ceil(tmp);
    }

    this.setState({
      containerHTML,
      maxPage,
      card,
    });
  }

  handleNext() {
    const newValue = this.state.currentPage + 1;
    this.setState({
      ...this.state,
      currentPage: newValue,
    });
  }

  handlePrev() {
    const newValue = this.state.currentPage - 1;
    this.setState({
      ...this.state,
      currentPage: newValue,
    });
  }

  render() {
    // handle page
    let element = this.state.containerHTML;
    if (element) {
      let x =
        (this.state.currentPage - 1) *
        this.state.containerHTML.offsetWidth *
        -1;
      if (this.state.currentPage === 1) {
        x = 0;
      }
      element.style.transform = `translate3d(${x}px,0px,0px)`;
    }

    return (
      <section
        className="mt-[100px] w-full container-slideshow relative sections"
        id="repository"
      >
        {/* <img src="../svg/repository.svg" alt="" className="hiasan-repo" /> */}
        <Logo className="hiasan-repo" />

        <div className="text-center">
          <h2 className="headline text-center" id="repository">
            Repository
          </h2>
          <p className="opacity-80 text-center mb-[50px]">
            Here are some projects I've worked on
          </p>
        </div>

        <div className="relative w-[85%] mx-auto">
          <div className="container-swiper">
            <div className="container-card my-[15px]">
              {/* card section */}
              <CardRepository cards={this.state.cards} />
            </div>
          </div>

          {/* button section */}
          <div className="">
            {this.state.currentPage > 1 && (
              <button
                className="circle-btn circle-btn-left hilang"
                onClick={this.handlePrev}
              >
                <span className="material-symbols-outlined">
                  arrow_back_ios
                </span>
              </button>
            )}

            {this.state.currentPage < this.state.maxPage && (
              <button
                className="circle-btn circle-btn-right"
                onClick={this.handleNext}
              >
                <span className="material-symbols-outlined">
                  arrow_forward_ios
                </span>
              </button>
            )}
          </div>
        </div>
      </section>
    );
  }
}

export default RepositorySection;
