import React, { useState } from "react";

const ContactSection = () => {
  const [state, setState] = useState();

  const handleChange = (e) => {
    const newState = { [e.target.name]: e.target.value };

    setState({
      ...state,
      ...newState,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    window.location.href = `mailto:zoneandika@gmail.com?subject=${state.subject}&body=${state.body}`;
  };

  return (
    <section className="mt-[100px] sections w-full" id="contact">
      <div className="text-center">
        <h2 className="headline">Contact me</h2>
        <p className="opacity-80">You can contact me using the form below.</p>
      </div>

      <form className="form mb-[200px]" onSubmit={handleSubmit}>
        <div className="mb-[100px]">
          <p className="text-center opacity-50">
            don't hesitate to contact me even if it's just saying hello
          </p>
        </div>
        <div className="flex flex-col gap-[5px]">
          <div className="w-[80%] mx-auto">
            <label htmlFor="subjek">Subject :</label>
            <input
              name="subject"
              type="text"
              className="input-text h-[35px]"
              id="subject"
              onChange={handleChange}
            />
          </div>
          <div className="w-[80%] mx-auto">
            <label htmlFor="subjek">Body :</label>
            <textarea
              name="body"
              className="input-text"
              id="content"
              onChange={handleChange}
            ></textarea>
          </div>
        </div>
        <div className="flex items-center justify-center pt-[50px]">
          <button type="submit" className="btn">
            send
          </button>
        </div>
      </form>
    </section>
  );
};

export default ContactSection;
