import React, { useState } from "react";

function CardRepository(props) {
  let [state] = useState({ cards: props.cards });

  const card = state.cards.map((card) => {
    return (
      <div className="card">
        <div className="card-repo" key={card.id}>
          <div className="relative">
            <img className="w-[100%] rounded-t-[9px]" src={card.img} alt="" />
            <ul className="text-white flex gap-[10px] absolute bottom-[0] ml-[10px] mb-[7px]">
              {/* looping tag */}
              {card.tag.map((tag) => {
                return (
                  <li>
                    <p className="tag">{tag}</p>
                  </li>
                );
              })}
            </ul>
          </div>

          <div className="px-[15px] py-[10px]">
            <h2>{card.name}</h2>
            <p>{card.des}</p>
            <div className="mt-[15px] flex gap-[15px] justify-center absolute bottom-3 w-fit mx-auto right-0 left-0">
              <button className="btn">result</button>
              <button className="btn inline w-fit">source code</button>
            </div>
          </div>
        </div>
      </div>
    );
  });

  return <>{card}</>;
}

export default CardRepository;
